<template>
  <div class="content">
  <div class="operation"> 
    <el-form :inline="true" label-width="100px" label-position="right">
      <el-form-item label="所属店铺">
        <el-select v-model="tempData.shopName"></el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchList" icon="el-icon-search">查询</el-button>
      </el-form-item>
    </el-form>
  </div>
  <div class="table">
    <el-table :data="tableData" v-loading="loading">
      <el-table-column label="订单号" prop="orderNumber"></el-table-column>
      <el-table-column label="店铺名称" prop="shopName"></el-table-column>
      <el-table-column label="商品信息" prop="busProductInfo"></el-table-column>
      <el-table-column label="催单时间" prop="createTime"></el-table-column>
      <el-table-column label="催单状态" prop="reminderStatus">
        <template slot-scope="user">{{statuList[user.row.reminderStatus]}}</template>
      </el-table-column>
    </el-table>
  </div>
    <pagination
      :current-page="page.pageCurrent"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    ></pagination>
  </div>
</template>

<script>
import pagination from "@/components/pagination";
import { order } from "@/api/index.js";
export default {
  components: {
    pagination
  },
  data() {
    return {
      statuList:['未处理','处理中','已处理'],
      //加载状态
      loading: false,
      //表格数据
      tableData: [],
      //搜索时临时数据
      tempData: {},
      //搜索时用的数据
      searchData: {},
      //分页数据
      page: {
        pageNum: null,
        pageCurrent: 1,
        pageSize: 10
      }
    };
  },
  methods: {
    initData: function(page, size) {
      this.loading = true;
      this.searchData = this.$common.deepCopy(this.tempData);
      this.searchData.page = this.page.pageCurrent;
      this.searchData.size = this.page.pageSize;
      order.reminder(this.searchData).then(res => {
        this.tableData = res.rows;
        this.page.pageNum = res.count;
      });
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    searchList: function() {
      this.searchData = this.$common.deepCopy(this.tempData);
      this.page.pageCurrent = 1;
      this.initData(1);
    },
    //页数改变时的回调
    currentChange(val) {
      this.page.pageCurrent = val;
      this.initData();
    },
    //每页总数改变时的回调
    sizeChange(val) {
      this.page.pageSize = val;
      this.page.pageCurrent = 1;
      this.initData(1);
    }
  },
  beforeMount() {
    this.initData();
  }
};
</script>

<style lang="scss" scoped>
   .operation{
     .el-form-item{
        margin-bottom: 0;
     }
    
   }
</style>




